















































import { defineComponent } from "@vue/composition-api";
import ProjectCard from "@c/molecules/project/ProjectCard.vue";
import EventCard from "@c/molecules/event/EventCard.vue";
import NoteCard from "@c/molecules/note/NoteCard.vue";
import PlaceCard from "@c/molecules/place/PlaceCard.vue";

export default defineComponent({
  name: "NetSlide",

  components: {
    ProjectCard,
    PlaceCard,
    EventCard,
    NoteCard
  },

  props: {
    items: {
      type: Array,
      default: () => []
    },
    type: {
      type: String
    },
    title: {
      type: String
    },
    empty: {
      type: String
    },
    loading: {
      type: Boolean,
      default: false
    },
    withPlace: {
      type: Boolean,
      default: false
    },
    all: String
  },

  setup() {
    return {};
  }
});
